<!--
  function reservé à l'administrateur.
-->
<template>
  <div></div>
</template>

<script>
/**
 * Jquery doit etre definie;
 */
var $;
if (window.jQuery) {
  $ = window.jQuery;
} else if (window.$) {
  $ = window.$;
}

export default {
  name: "Localisation",
  mounted() {
    /**
     * On verifie si les parametres de l'utilisateur ( @IP, pays, ville ...) sont deja en cache.
     */
    if (!localStorage.getItem("wbu-userinfo_localisation")) {
      this.getUserInfos();
    } else {
      this.$emit(
        "get_user_localisation",
        localStorage.getItem("wbu-userinfo_localisation")
      );
    }
  },
  methods: {
    getUserInfos() {
      var self = this;
      $.getJSON("https://ipapi.co/json/", function(data) {
        localStorage.setItem("wbu-userinfo_localisation", JSON.stringify(data));
        console.log("wbuuserinfo : ", JSON.stringify(data, null, 2));
        console.log("wbuuserinfo2 : ", JSON.stringify(data));
        self.$emit("get_user_localisation", data);
      });
    }
  }
};
</script>
